@import "../Main.scss";

.gradient-button {
  cursor: pointer;
  display: inline-block;
  user-select: none;
  font-family: $main-font-family;
  gap: 20px;
  font-size: 1.3rem;
  padding: 0.8rem 3.2rem;
  border: 2px solid #ffffff88;
  border-radius: 30px;
  color: white;
  background-image: none;
  background-color: #ffffff00;
  transition: color 0.5s, background-color 0.5s, 0.5s, border-color 0.5s,
    box-shadow 0.5s;
}
.gradient-button:hover {
  color: white;
  background-image: linear-gradient(
    120deg,
    rgba(77, 181, 147, 1) 20%,
    rgba(39, 55, 159, 1) 80%
  );
  box-shadow: 20px 20px 10px 0px #22222233;
}
.gradient-button:active {
  border-color: #333333;
}
