@import "../Main.scss";

.login-button {
  cursor: pointer;
  font-family: $main-font-family;
  gap: 20px;
  font-size: 1.1rem;
  min-width: 160px;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
  padding-left: 0.4rem;
  padding-right: 0.8rem;
  border: 2px solid #ffffff88;
  border-radius: 30px;
  color: white;
  background-image: none;
  background-color: #ffffff00;
  i {
    font-size: 1.7rem;
    padding: 8px;
    background-image: linear-gradient(
      120deg,
      rgba(77, 181, 147, 1) 20%,
      rgba(39, 55, 159, 1) 80%
    );
    border-radius: 30px;
    transition: background-color 0.5s, background-image 0.5s;
  }
  transition: color 0.5s, background-color 0.5s, 0.5s, border-color 0.5s,
    box-shadow 0.5s;
}
.login-button:hover {
  color: white;
  background-image: linear-gradient(
    120deg,
    rgba(77, 181, 147, 1) 20%,
    rgba(39, 55, 159, 1) 80%
  );
  i {
    background-color: #ffffff00;
    background-image: none;
  }
  box-shadow: 20px 20px 10px 0px #22222233;
}
.login-button:active {
  border-color: #333333;
}
