.transparent-card {
  position: relative;
  display: inline-block;
  width: 320px;
  height: 320px;
  box-shadow: inset 2em 2em 2em 0 rgba(255, 255, 255, 0.2),
    inset -3em -3em 3em 0 rgba(0, 0, 0, 0.1);
  border: 2px solid #ffffff77;
  border-radius: 20px;

  .transparent-card-icon {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, 0);
  }
  .transparent-card-text {
    position: absolute;
    bottom: 15%;
    left: 50%;
    transform: translate(-50%, 50%);
    color: white;
    font-family: "Oswald";
    font-size: 30px;
    font-weight: bolder;
  }
  .transparent-card-arrow {
    color: white;
    font-size: 40px;
    position: absolute;
    right: -25%;
    top: 50%;
    transform: (-50%, -50%);
  }
}
