.introduction-container {
  margin-top: 160px;
  position: relative;

  .introduction-balloon-left {
    position: absolute;
    left: -9%;
    top: 77%;
    width: 10%;
    height: auto;
  }
  .introduction-balloon-right {
    position: absolute;
    right: -8%;
    top: -20%;
    width: 10%;
    height: auto;
    transform: translate(0%, 0%);
  }
  .introduction-caption {
    margin-top: 40px;
    .introduction-badge {
      margin-left: 25px;
      padding: 10px;
      background-color: #ffffff40;
      font-family: "proxima-nova Semibold", sans-serif;
      font-size: 1.5rem;
      letter-spacing: 5px;
      color: white;
    }
    .introduction-title {
      margin-top: 25px;
      font-size: 7vw;
      font-family: "Poppins", sans-serif;
      font-weight: 900;
      line-height: 0.9;
      color: white;
    }

    .introduction-description {
      margin-left: 15px;
      color: white;
      font-size: 1.7rem;
      font-family: "proxima-nova Semibold", sans-serif;
    }
    @media screen and (max-width: 992px) {
      .introduction-title {
        font-size: 50px;
      }
      .introduction-description {
        font-size: 20px;
      }
    }
    .introduction-button-group {
      margin-top: 25px;
    }
  }
  .introduction-price {
    margin-top: 20px;
    .introduction-price-pane {
      position: relative;
      z-index: 1;
      float: right;
      margin-right: 12%;
      box-shadow: inset 2em 2em 2em 0 rgba(255, 255, 255, 0.2),
        inset -3em -3em 3em 0 rgba(0, 0, 0, 0.1);
      width: 60%;
      border: 1px solid #ffffff80;
      border-radius: 30px;
      padding-top: 60px;
      padding-left: 30px;
      padding-right: 30px;
      padding-bottom: 20px;
      .introduction-price-title {
        float: left;
        color: white;
        font-weight: bolder;

        font-family: "Oswald";
        font-size: 42px;
      }
      .introduction-price-unit {
        margin-top: 10px;
        margin-left: 5px;
        float: right;
        color: white;
        font-weight: bolder;
        font-family: "Oswald";
        font-size: 24px;
        padding: 5px 30px;
        background-color: #ffffff44;
        border-radius: 30px;
      }
      .introduction-price-dollar-title {
        clear: both;
        margin-top: 75px;
        margin-left: 5px;
        color: white;
        font-weight: bold;
        font-family: "Proxima Nova";
        font-size: 18px;
      }
      .introduction-price-dollar-value {
        color: white;
        font-family: "Oswald";
        font-weight: bolder;
        font-size: 41px;
      }
      .introduction-price-change-pane {
        margin-top: 15px;
        border: 1px solid #ffffff40;
        border-radius: 15px;
        background-color: #ffffff10;
        padding: 10px 20px;
        .introduction-price-change-title {
          font-family: "Proxima Nova";
          font-size: 16px;
          color: white;
        }
        .introduction-price-change-value {
          font-family: "Oswald";
          font-size: 35px;
          font-weight: bolder;
          color: white;
        }
      }
      .introduction-price-capital-pane {
        margin-top: 15px;
        border: 1px solid #ffffff40;
        border-radius: 15px;
        background-color: #ffffff10;
        padding: 10px 20px;
        .introduction-price-capital-title {
          font-family: "Proxima Nova";
          font-size: 16px;
          color: white;
        }
        .introduction-price-capital-value {
          font-family: "Oswald";
          font-size: 35px;
          font-weight: bolder;
          color: white;
        }
      }
      .introduction-price-circle {
        position: absolute;
        width: 60px;
        height: 60px;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 50%);
        border: 1px solid #ffffff;
        background: rgb(129, 180, 189);
        background: rgb(141, 196, 191);
        background: rgb(228, 255, 253);
        background: rgb(228, 255, 253);
        background: linear-gradient(
          129deg,
          rgba(228, 255, 253, 1) 0%,
          rgba(141, 196, 191, 1) 26%,
          rgba(39, 95, 152, 1) 58%,
          rgba(19, 34, 79, 1) 81%
        );
        border-radius: 30px;
      }
      .introduction-price-pane-background {
        position: absolute;
        z-index: -1;
        width: 140%;
        height: auto;
        bottom: 0;
        left: 0;
        transform: translate(-32%, 20%);
      }
      .introduction-price-pane-stripe {
        position: absolute;
        z-index: -1;
        width: 40%;
        height: auto;
        top: -40%;
        left: -40%;
        transform: translate(-100% -100%);
      }
    }
  }
}
