.ada-info-container {
  margin-top: 100px;
  background-color: #14205a;
  border: 2px solid #5cd1a9;
  border-radius: 50px;
  padding-left: 15%;
  padding-top: 100px;
  padding-bottom: 100px;
  padding-right: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
  .ada-info-text-background {
    position: absolute;
    z-index: 0;
    top: 20%;
    left: 0%;
    width: 100%;
    transform: translate(0%, -50%);
    height: 100%;
    width: auto;
    font-family: "Oswald";
    font-size: 400px;
    font-weight: 900;
    color: #172465;
  }
  .ada-info-balloon-left {
    position: absolute;
    left: -9%;
    top: -20%;
    width: 10%;
    height: auto;
  }
  .ada-info-balloon-right {
    position: absolute;
    right: -8%;
    top: -15%;
    width: 10%;
    height: auto;
    transform: translate(0%, 0%);
  }
  .ada-info-left {
    z-index: 1;
    flex: 6;
    .ada-info-title {
      font-family: "Poppins", sans-serif;
      font-size: 75px;
      font-weight: bolder;
      color: white;
    }
    .ada-info-button-group {
      margin-top: 20px;
      display: flex;
      gap: 20px;
    }
  }
  .ada-info-right {
    z-index: 1;
    flex: 4;
    display: flex;
    justify-content: flex-end;
    img {
      width: 100%;
      height: auto;
    }
  }
}
