@import "../Main.scss";

.gradient-chooser {
  cursor: pointer;
  display: inline-block;
  font-family: $main-font-family;
  user-select: none;
  gap: 20px;
  font-size: 1.1rem;
  padding: 0.8rem 3rem;
  border-radius: 30px;
  background-color: #ffffff50;
  background-image: none;
  color: white;
  border: 2px solid transparent;
  transition: color 0.5s, background-color 0.5s, 0.5s, border-color 0.5s,
    box-shadow 0.5s;
}
.gradient-chooser-selected,
.gradient-chooser:hover {
  color: white;
  border: 2px solid white;
  background-image: linear-gradient(
    120deg,
    rgba(77, 181, 147, 1) 20%,
    rgba(39, 55, 159, 1) 80%
  );
  box-shadow: 20px 20px 10px 0px #22222233;
}
