.footer-container {
  margin-top: 150px;
  .footer-menu {
    display: flex;
    justify-content: space-between;
    .footer-about {
      width: 35%;

      .footer-about-title {
        display: flex;
        align-items: center;
        gap: 10px;
        img {
          width: 70px;
          width: 70px;
        }
        .footer-about-title-text {
          font-family: "Oswald";
          color: white;
          font-size: 28px;
          font-weight: bolder;
        }
      }
      .footer-about-description {
        margin-top: 10px;
        font-family: "Proxima Nova";
        font-size: 18px;
        color: white;
      }
    }
    .footer-title {
      margin-top: 20px;
      margin-bottom: 28px;
      color: white;
      font-family: "Poppins";
      font-size: 25px;
      font-weight: bolder;
    }
    .footer-link {
      user-select: none;
      cursor: pointer;
      margin-bottom: 10px;
      color: white;
      font-family: "Poppins";
      font-size: 18px;
    }
    .footer-link:hover {
      text-decoration: underline;
    }
    .footer-link:active {
      text-decoration: none;
    }
    .footer-marketplace {
      margin-left: 30px;
      margin-right: 30px;
    }
    .footer-acount {
      margin-left: 30px;
      margin-right: 30px;
    }
    .footer-resources {
      margin-left: 30px;
      margin-right: 30px;
    }
    .footer-company {
      margin-left: 30px;
      margin-right: 30px;
    }
    margin-bottom: 150px;
  }
  @media screen and (max-width: 768px) {
    .footer-menu {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  .footer-copyright {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: "Poppins";
    font-size: 16px;
    padding-top: 40px;
    padding-bottom: 40px;
    border-top: 1px solid white;
  }
}
