.token-card {
  border-radius: 30px;
  width: 320px;
  height: 480px;
  background-color: #121f57;
  position: relative;
  cursor: pointer;

  .token-card-image {
    overflow: hidden;
    position: absolute;
    border-radius: 30px;
    height: 69%;
    width: 100%;
    .token-card-image-background {
      position: absolute;
      top: 70%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 150%;
      width: auto;
    }

    .token-card-image-info {
      position: absolute;
      top: 82%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 20px;
      width: 90%;
      border-radius: 15px;
      background-color: #000000d0;
      opacity: 0;
      padding: 15px;
      transition: opacity 0.5s;
      .token-card-image-info-avatar {
        overflow: hidden;
        border-radius: 55px;
        border: 2px solid white;
        img {
          width: 55px;
          height: 55px;
        }
      }
      .token-card-image-info-text {
        font-family: "Proxima Nova";
        font-size: 18px;
        font-weight: bold;
        color: white;
      }
    }
  }

  .token-card-bottom {
    position: absolute;
    bottom: 5%;
    margin-left: 30px;
    margin-right: 30px;
    .token-card-bottom-first {
      display: flex;
      align-items: center;
      .token-card-title {
        font-family: "Poppins", sans-serif;
        font-size: 20px;
        color: white;
      }
      .token-card-price {
        flex: 4;
        .token-card-price-value-unit {
          float: right;
          .token-card-price-value {
            font-family: "Poppins", sans-serif;
            font-size: 30px;
            color: #00dea2;
            font-weight: bolder;
          }
          .token-card-price-unit {
            position: relative;
            bottom: 2px;
            font-family: "Poppins", sans-serif;
            font-size: 13px;
            color: #00dea2;
            font-weight: bolder;
          }
        }
      }
    }
    .token-card-bottom-second {
      margin-top: 5px;
      display: flex;
      justify-content: space-between;
      gap: 10px;
      .token-card-favourite-group {
        display: flex;
        gap: 10px;
        .token-card-favourite {
          display: inline-block;
          position: relative;
          .token-card-favourite-value {
            position: absolute;
            top: 40%;
            left: 50%;
            transform: translate(-50%, -50%);
            color: white;
            font-family: "Poppins", sans-serif;
            font-size: 12px;
            font-weight: bolder;
          }
        }
        .token-card-favourite-description {
          font-family: "Poppins", sans-serif;
          font-size: 14px;
          color: white;
          width: 80px;
        }
      }

      .token-card-button {
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: white;
        width: 40px;
        height: 40px;
        border-radius: 40px;
        border: 2px solid transparent;
        transition: color 0.5s, background-color 0.5s, border-color 0.5s;
        .token-card-button-text {
          color: #2b6a9a;
          font-family: "Poppins", sans-serif;
          font-size: 30px;
          font-weight: bolder;
          padding: 20px;
          user-select: none;
          transition: color 0.5s, background-color 0.5s, border-color 0.5s;
        }
      }
      .token-card-button:hover {
        background-image: linear-gradient(
          120deg,
          rgba(77, 181, 147, 1) 20%,
          rgba(39, 55, 159, 1) 80%
        );
        border: 2px solid white;
        .token-card-button-text {
          color: white;
        }
      }
      .token-card-button:active {
        background-color: white;
        border: 2px solid transparent;
        .token-card-button-text {
          color: #2b6a9a;
        }
      }
    }
  }
}
.token-card:hover {
  .token-card-image-info {
    opacity: 1;
  }
}
