.royalties-container {
  position: relative;
  margin-top: 310px;
  margin-left: 10px;

  .royalties-title {
    color: white;
    font-family: "Oswald";
    font-size: 50px;
    font-weight: bolder;
  }
  .royalties-description {
    max-width: 70%;
    margin-top: 25px;
    color: white;
    font-family: "Proxima Nova";
    font-size: 25px;
    line-height: 1.3;
  }
  .royalties-card-group {
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;
    gap: 120px;
  }
  .royalties-background {
    position: absolute;
    top: 5%;
    left: -2%;
    font-weight: bolder;
    transform: translate(0, -50%);
    width: 100%;
    z-index: -1;
    img {
      width: 100%;
      height: 235px;
    }
  }
}
