.token-gallery-container {
  margin-top: 250px;
  margin-left: 0px;
  width: 100%;
  position: relative;
  .token-gallery-background {
    position: absolute;
    left: -9%;
    top: -40%;
    width: 30%;
    height: auto;
    z-index: -1;
  }
  .token-gallery-text-background {
    position: absolute;
    top: 70%;
    left: 45%;
    transform: translate(-50%, -50%);
    height: 150%;
    width: auto;
    font-family: "Poppins";
    font-size: 100px;
    font-weight: bold;
    z-index: -1;
    color: #275a9a;
  }
  .token-header {
    display: flex;
    justify-content: space-between;
    margin-right: 20px;
    .token-gallery-title {
      color: white;
      font-family: "Poppins", sans-serif;
      font-weight: bolder;
      font-size: 45px;
    }
    .token-gallery-chooser-group {
      .gradient-chooser {
        margin-left: 10px;
      }
    }
  }

  .token-gallery-card-group {
    margin-top: 40px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    .token-card {
      margin-left: 17px;
      margin-bottom: 10px;
    }
  }
}
