.home-container {
  position: relative;
  overflow: hidden;
}
.home-background {
  position: absolute;
  z-index: -2;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgb(74, 159, 146);
  background: linear-gradient(
    180deg,
    rgba(74, 159, 146, 1) 0%,
    #264b93 50%,
    rgba(37, 54, 160, 1) 81%
  );
  //background-image: url("http://localhost:3000/assets/background.png");
}
